/**
 * Wrapper
 */

.wrapper {
  box-sizing: border-box;
  padding: 0 20px;
  max-width: 100%;
  width: 100%;

  @include respond-to('medium') {
    padding: 0 40px;
  }

  @include respond-to('large') {
    margin: 0 auto;
    width: 980px;
  }

  @include respond-to('huge') {
    width: 1160px;
  }
}
